import { JobType } from "./enums/enums";

export type RequestJob<T> = {
  jobType?: JobType;
  event: string;
  trackingConsentMessageId: string;
  data?: Record<string, any>;
  headers?: Record<string, any>;
  flushEventBefore: boolean;
  // eslint-disable-next-line no-unused-vars
  resolve?: (value: T | PromiseLike<T>) => void;
  // eslint-disable-next-line no-unused-vars
  reject?: (reason?: any) => void;
};

export class QueueManager<T> {
  private jobs: RequestJob<T>[] = [];
  private running = false;

  // eslint-disable-next-line no-unused-vars
  public callback: (job: RequestJob<T>) => Promise<T>;

  // eslint-disable-next-line no-unused-vars
  constructor(callback: (job: RequestJob<T>) => Promise<T>) {
    this.callback = callback;
  }

  enqueueJob(job: RequestJob<T>): Promise<T> {
    return new Promise<T>((resolve, reject) => {
      job.reject = reject;
      job.resolve = resolve;
      this.jobs.push(job);
      if (!this.running) {
        this.runQueue();
      }
    });
  }

  private async runQueue(): Promise<void> {
    this.running = true;
    while (this.jobs.length > 0) {
      const job = this.jobs.shift();
      try {
        const result = await this.callback(job);
        job.resolve(result);
      } catch (error) {
        job.reject(error);
      }
    }
    this.running = false;
  }
}
