import { HTTPClient } from "./httpclient";
import { ITrackerResponse } from "./interface/itracker_response";
import { IConsentMessage, ConsentMessage } from "./interface/consent_message";

export class PamAPI {
  private http: HTTPClient;

  constructor(baseAPIURL: string) {
    this.http = new HTTPClient(baseAPIURL);
  }

  private getPageURL() {
    return window.document.location && window.document.location.href;
  }

  private getDefaultPayload(): Record<string, any> {
    return {
      page_language: window.navigator.language,
      page_referrer: window.document.referrer,
      page_title: window.document.title,
      page_url: decodeURI(this.getPageURL()),
      platform: "browser",
      user_agent: window.navigator.userAgent,
    };
  }

  async postTracker(
    event: string,
    contactId: string,
    trackingConsentMessageId: string,
    database: string,
    formFields: Record<string, any>,
    headers: Record<string, any>
  ): Promise<ITrackerResponse> {
    const payload = this.getDefaultPayload();
    payload.event = event;

    const form_fields = { ...formFields };
    form_fields._consent_message_id = trackingConsentMessageId;
    form_fields._database = database;
    payload.form_fields = {
      ...form_fields,
      _contact_id: contactId,
      _database: database,
    };

    const response = await this.http.post("/trackers/events", payload, headers);
    return response;
  }

  async loadConsentDetails(
    consentMessageIDs: string[]
  ): Promise<Record<string, ConsentMessage>> {
    const result: Record<string, ConsentMessage> = {};

    while (consentMessageIDs.length > 0) {
      const id = consentMessageIDs.shift();
      const response = await this.http.get(`/consent-message/${id}`, {});
      result[id] = new ConsentMessage(response);
    }

    return result;
  }
}
